import {mapState, mapMutations} from 'vuex'

export default {	
	install(Vue) {
		Vue.mixin({
			data() {
				return {
					
				}
			},
			methods: {
				checkedisLogin() {
					// 
					return new Promise(resolve => {
						if (this.userInfo) {	// userInfo
							resolve()
						} else {	// 
							uni.navigateTo({
								url: '/pages/userInfo/login/login',
								fail(error) {
									console.log(error)
								}
							})
						}
					})
				},
				...mapMutations(['updateUserInfo'])
			},
			computed: {
				...mapState(['userInfo'])
			}
		})
	}
}
