export default ({name, data={}}) => {
	// Promise
	return new Promise((resolve, reject) => {
		// Loading
		uni.showLoading({})
		// uniCloud
		uniCloud.callFunction({
			name,	// 
			data,
			success({result}) {	   // 
				if (result.code === 0) {	// 
					resolve(result.data)
				} else {
					uni.showToast({		// 
						icon: "none",
						title: result.msg
					})
				}
			},
			fail(err) {		// 
				reject(err)
			},
			complete() {	// 
				uni.hideLoading()	// Loading
			}
		})
	})
}